import React, { useState, useEffect } from 'react'
import {
  StylesProvider,
  Container,
  TextField,
  IconButton,
  Button,
  Grid,
  Card,
} from '@material-ui/core'
import './PostTweet.css'
import { PhotoCamera } from '@material-ui/icons'
import ImageIcon from '@material-ui/icons/Image'
import ImageSearchIcon from '@material-ui/icons/ImageSearch'

function Post({
  image,
  setMessage,
  message,
  setImage,
  createPost,
  onChangePicture,
  avatar,
}) {
  return (
    <StylesProvider injectFirst>
      <Container style={{ paddingTop: '2rem', paddingBottom: '2rem' }}>
        <Card className="card-post">
          <Grid container>
            <Grid item xs={1} sm={1}>
              <img
                src={`https://avatars.dicebear.com/api/avataaars/${avatar}.svg?background=%230000ff`}
                alt="img-profile"
                id="person"
              />
            </Grid>
            <Grid item xs={11} sm={11}>
              <form className="form" noValidate autoComplete="off">
                <h3>
                  {message ? (
                    ''
                  ) : (
                    <p>Share your memes and let the world know your talent</p>
                  )}
                </h3>

                <TextField
                  fullWidth
                  id="outlined-basic"
                  className="input-form"
                  placeholder=""
                  defaultValue=""
                  onChange={(e) => setMessage(e.target.value)}
                  multiline
                  minRows={4}
                  required
                />
                {image ? (
                  <img
                    src={URL.createObjectURL(image)}
                    alt="img-preview"
                    style={{ width: '100%' }}
                  />
                ) : (
                  ''
                )}
                <br />
                <label htmlFor="icon-button-phot">
                  <IconButton color="primary" component="span">
                    <PhotoCamera />
                    <ImageIcon />
                    <ImageSearchIcon />
                  </IconButton>
                </label>
                <input
                  accept="image/*"
                  className="input"
                  id="icon"
                  defaultValue={image}
                  onChange={(e) => onChangePicture(e)}
                  // onChange={(e) => setImage(e.target.files[0]) }
                  type="file"
                />
                <Button
                  size="medium"
                  variant="contained"
                  color="primary"
                  onClick={createPost}
                  className="post-btn"
                >
                  Post
                </Button>
              </form>
            </Grid>
          </Grid>
        </Card>
      </Container>
    </StylesProvider>
  )
}

export default Post
