import React, { useState, useEffect } from 'react'
import './main.css'
import Tweet from '../tweet/tweet'
import PostTweet from '../post-tweet/PostTweet'
import { SkynetClient, genKeyPairFromSeed } from 'skynet-js'
import dummy from '../images/dummy.png'
import VerifiedUserSharpIcon from '@material-ui/icons/VerifiedUserSharp'
import { Button } from '@material-ui/core'
const portal = 'https://siasky.net/'
const client = new SkynetClient(portal)
const SEEDPHASE = 'seedphase'
const { privateKey, publicKey } = genKeyPairFromSeed(SEEDPHASE)
const dataKey = 'datakey'

const Main = ({ unstoppableUsername, avatar, walletAddress, userLogOut }) => {
  console.log(
    'ðŸš€ ~ file: main.js ~ line 16 ~ Main ~ unstoppableUsername, avatar, walletAddress, userLogOut',
    unstoppableUsername,
    avatar,
    walletAddress,
    userLogOut,
  )
  const [message, setMessage] = useState('')
  const [image, setImage] = useState('')
  const [posts, setPosts] = useState('')
  const [imgData, setImgData] = useState('')

  useEffect(() => {
    if (unstoppableUsername) {
    }
    fetchData()
  }, [])

  const onChangePicture = async (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0])
      const reader = new FileReader()
      reader.addEventListener('load', () => {
        setImgData(reader.result)
      })
      reader.readAsDataURL(e.target.files[0])
    }
  }

  const createPost = async () => {
    if (!imgData && !message) {
      alert('A photo & a description are required!')
    } else {
      const dataImage = await client.uploadFile(image)
      const imgLink = dataImage.skylink

      console.log(
        'unstoppableUsername, avatar, walletAddress, message, imgData, image',
        unstoppableUsername,
        avatar,
        walletAddress,
        message,
        imgData,
        image,
      )

      // skylinks start with `sia://` and don't specify a portal URL
      // generate URLs for our current portal though.
      const skylinkUrl = await client.getSkylinkUrl(imgLink)
      const obj = {
        image: skylinkUrl,
        description: message,
        avatar: `https://avatars.dicebear.com/api/avataaars/${avatar}.svg?background=%230000ff`,
        author: unstoppableUsername,
        walletAddress: walletAddress,
        timeCreated: getTime(),
      }
      let { data } = await client.db.getJSON(publicKey, dataKey)
      if (data?.length) {
        data.unshift(obj)
      } else {
        data = [obj]
      }
      // add data to skynet
      const result = await client.db.setJSON(privateKey, dataKey, data)
      window.location.reload()
    }
  }

  const getTime = () => {
    let monthNames = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec',
    ]

    let date = new Date()

    let day = date.getDate()

    let monthIndex = date.getMonth()
    let monthName = monthNames[monthIndex]

    let year = date.getFullYear()
    return `${day}-${monthName}-${year}`
  }

  const fetchData = async () => {
    let { data } = await client.db.getJSON(publicKey, dataKey)
    setPosts(data)
  }

  return (
    <div id="container-main">
      <div className="container-flex">
        <p
          style={{
            fontSize: '1.1rem',
            fontWeight: '500',
            color: ' #ba03c4',
            paddingLeft: '3rem',
          }}
        >
          Welcome <span>{unstoppableUsername}</span>
        </p>
        <div>
          <Button
            variant="contained"
            style={{ backgroundColor: '#c506e8', color: 'white' }}
            color="secondary"
            endIcon={<VerifiedUserSharpIcon />}
          >
            {walletAddress
              ? `${walletAddress.substring(0, 5)}...${walletAddress.substring(
                  32,
                )}`
              : ''}
          </Button>
          <Button onClick={() => userLogOut()}>Logout</Button>
        </div>
      </div>

      <PostTweet
        image={image}
        message={message}
        setMessage={setMessage}
        setImage={setImage}
        createPost={createPost}
        onChangePicture={onChangePicture}
        avatar={avatar}
      />

      <Tweet posts={posts} />
      <img src={dummy} alt="dummy" style={{ width: '100%' }} />
    </div>
  )
}

export default Main
