import React, { createRef } from 'react'
import {
  Button,
  Container,
  StylesProvider,
  TextField,
  MenuItem,
} from '@material-ui/core'
import noImageProfile from '../images/noImageProfile.jpg'
import './Login.css'

function Login({ setAvatar, avatar, setAccount, unstoppableDomainLogin }) {
  const avatarType = createRef()
  
  return (
    <StylesProvider injectFirst>
      <br />
      <Container className="login-container">
        <center>
          <div className="login-text-field">
            <TextField
              fullWidth
              name="profileType"
              select
              label="Select Avatar"
              variant="outlined"
              onChange={(e) => setAvatar(e.target.value)}
              defaultValue=""
              ref={avatarType}
            >
              <MenuItem value="micah">Micah</MenuItem>
              <MenuItem value="avataaars">Avataaars</MenuItem>
              <MenuItem value="human">Human</MenuItem>
              <MenuItem value="male">Male</MenuItem>
              <MenuItem value="female">Female</MenuItem>
              <MenuItem value="bottts">Bottts</MenuItem>
              <MenuItem value="identicon">Identicon</MenuItem>
              <MenuItem value="jdenticon">Jdenticon</MenuItem>
            </TextField>
          </div>

          {avatar ? (
            <img
              src={`https://avatars.dicebear.com/api/${avatar}/seed.svg?background=%230000ff`}
              alt="img-profile"
              className="login-profile-img"
            />
          ) : (
            <img
              src={noImageProfile}
              alt="profile"
              className="login-profile-img"
            />
          )}
          <br />
          <br />
          <br />
          <Button
            variant="contained"
            className="mint-btn"
            onClick={() => unstoppableDomainLogin()}
          >
            Connect with Unstoppable Domains
          </Button>
        </center>
      </Container>
    </StylesProvider>
  )
}

export default Login
